@use "../../../scss/public/grid" as *;

@keyframes appear-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.root-container {
  position: relative;
  padding-top: 40px;

  &:hover {
    .right-icon {
      visibility: visible;

      animation-name: appear-in;
      animation-duration: 0.3s;
      animation-fill-mode: both;
    }
  }
}

.top-icon {
  position: absolute;

  left: 50%;
  transform: translateX(-50%) translateY(-40px);

  &--muted {
    opacity: 0.25;
  }
}

.right-icon {
  visibility: hidden;
  position: absolute;
  top: 64px;
  right: 24px;
}

.container {
  padding: 0 24px 24px 24px;
  height: 100%;

  @include p-size-mobile {
    height: fit-content;
  }
}
